import React from 'react';
import style from './Main.module.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faGlobe } from '@fortawesome/free-solid-svg-icons'



function Header() {
    return (
        <div className={style.mainBlockWrapper}>
            <div className={style.mainBlockContainer}>
                <div className={`${style.row} ${style.noGutters} ${style.vFull}`}>
                    <div className={style.leftBlockWrapper}>
                        <div className={`${style.home} ${style.mainContent} ${style.active}`}>
                            <div className={style.sideBlock}>
                                <div className={style.logo}>
                                    <FontAwesomeIcon icon={faGlobe} />
                                </div>
                                <div className={style.textBlock}>
                                </div>
                            </div>
                            <div className={style.contentBlock}>
                                <div className={style.headContentBlock}>
                                    <span>Hi There</span>
                                    <h1>I am Tanya</h1>
                                    <p>Frontend Developer</p>
                                </div>
                                <div className={style.contentBlock}>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className={style.rightBlockWrapper}>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Header;
